export default function getAlert(alert) {
  const allAlerts = {
    startLocationWatch: {
      id: 1000,
      msg: "    ",
    },
    cancelLocationWatch: {
      id: 1001,
      msg: "    ",
    },
    enableLocationWatch: {
      id: 1002,
      msg: "    GPS?",
    },
  };

  const alertMsg = (
    allAlerts[alert].id +
    " " +
    allAlerts[alert].msg
  ).toString();

  window.alert(alertMsg);
}
